import { defineComponent, computed, ref, nextTick } from "vue";
import AcPageSearch from "@/components/page-search";
import AcPageContent from "@/components/page-content";
import AcPageModal from "@/components/page-modal";
import { tableConfig } from "./config/table-config";
import { formConfig } from "./config/search-config";
import { modalConfig } from "./config/modal-config";
import { usePageSearch } from "@/hooks/usePageSearch";
import { usePageModal } from "@/hooks/usePageModal";
import { menuMapLeafKeys } from "@/utils/map-menus";
import { useStore } from "@/store";
export default defineComponent({
  name: "RoleManager",
  components: {
    AcPageSearch,
    AcPageContent,
    AcPageModal
  },

  setup() {
    const [acPageContentRef, handleResetClick, handleQueryClick] = usePageSearch(); // 1. pageModal  hook

    const elfTreeRef = ref();

    const editCallback = item => {
      const leafKeys = menuMapLeafKeys(item.menuList);
      nextTick(() => {
        console.log(leafKeys);
        elfTreeRef.value?.setCheckedKeys(leafKeys, false);
      });
    };

    const [acModalRef, defaultInfo, handleCreateClick, handleEditClick] = usePageModal(undefined, editCallback);
    const store = useStore();
    const menus = computed(() => store.state.entireMenu);
    const otherInfo = ref({});

    const handleCheckChange = (data1, data2) => {
      const checkedKeys = data2.checkedKeys;
      const halfCheckedKeys = data2.halfCheckedKeys;
      const menuList = [...checkedKeys, ...halfCheckedKeys];
      otherInfo.value = {
        menuList
      };
      console.log(otherInfo.value);
    };

    return {
      formConfig,
      tableConfig,
      modalConfig,
      acModalRef,
      defaultInfo,
      menus,
      otherInfo,
      handleCreateClick,
      handleEditClick,
      acPageContentRef,
      handleResetClick,
      handleQueryClick,
      elfTreeRef,
      handleCheckChange
    };
  }

});